"use client"

import type React from "react"
import { createClient } from "@/lib/supabase/client"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import Link from "next/link"
import { useRouter } from "next/navigation"
import { useState } from "react"
import { AlertCircle, CheckCircle, Mail } from "lucide-react"

export default function SignUp() {
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [repeatPassword, setRepeatPassword] = useState("")
  const [fullName, setFullName] = useState("")
  const [error, setError] = useState<string | null>(null)
  const [isLoading, setIsLoading] = useState(false)
  const [signUpComplete, setSignUpComplete] = useState(false)
  const [step, setStep] = useState(1) // Declare the step variable
  const router = useRouter()

  const handleSignUp = async (e: React.FormEvent) => {
    e.preventDefault()
    const supabase = createClient()
    setIsLoading(true)
    setError(null)

    if (password !== repeatPassword) {
      setError("As senhas não coincidem")
      setIsLoading(false)
      return
    }

    if (password.length < 6) {
      setError("A senha deve ter pelo menos 6 caracteres")
      setIsLoading(false)
      return
    }

    try {
      console.log("[v0] Criando conta com email:", email)
      const { error: signUpError } = await supabase.auth.signUp({
        email,
        password,
        options: {
          emailRedirectTo: `${window.location.origin}/auth/login`,
          data: {
            full_name: fullName,
          },
        },
      })
      if (signUpError) throw signUpError
      console.log("[v0] Conta criada com sucesso")
      setSignUpComplete(true)
      setTimeout(() => {
        router.push("/auth/login")
      }, 3000)
    } catch (error: unknown) {
      const errorMsg = error instanceof Error ? error.message : "Erro ao criar conta"
      console.log("[v0] Erro ao criar conta:", errorMsg)
      setError(errorMsg)
    } finally {
      setIsLoading(false)
    }
  }

  if (signUpComplete) {
    return (
      <div className="flex min-h-screen w-full items-center justify-center p-4 sm:p-6 md:p-10 bg-gradient-to-br from-slate-950 via-slate-900 to-slate-950">
        <div className="w-full max-w-md text-center">
          <Card className="border-slate-700 bg-slate-900">
            <CardContent className="pt-12 pb-12">
              <div className="flex justify-center mb-6">
                <div className="w-20 h-20 rounded-full bg-green-500/20 border-2 border-green-500 flex items-center justify-center animate-pulse">
                  <CheckCircle className="w-12 h-12 text-green-400" />
                </div>
              </div>
              <h2 className="text-2xl font-bold text-white mb-4">Conta Criada com Sucesso!</h2>
              <p className="text-slate-400 mb-6">
                Enviamos um email de confirmação para <span className="font-semibold text-white">{email}</span>
              </p>
              <div className="bg-blue-500/10 border border-blue-500/50 rounded-lg p-4 mb-6">
                <p className="text-sm text-slate-300 flex items-start gap-2">
                  <Mail className="w-4 h-4 text-blue-400 mt-1 flex-shrink-0" />
                  <span>Clique no link no email para confirmar sua conta e fazer login</span>
                </p>
              </div>
              <p className="text-sm text-slate-400">Redirecionando em alguns segundos...</p>
            </CardContent>
          </Card>
        </div>
      </div>
    )
  }

  return (
    <div className="flex min-h-screen w-full items-center justify-center p-4 sm:p-6 md:p-10 bg-gradient-to-br from-slate-950 via-slate-900 to-slate-950">
      <div className="w-full max-w-2xl">
        <div className="space-y-6">
          {/* Onboarding Steps Header */}
          <div className="text-center space-y-2">
            <h1 className="text-4xl font-bold bg-gradient-to-r from-blue-400 to-emerald-400 bg-clip-text text-transparent">
              Bem-vindo!
            </h1>
            <p className="text-slate-400">Crie sua conta e comece a receber oportunidades de emprego</p>
          </div>

          {/* Onboarding Steps Visual */}
          <div className="grid grid-cols-3 gap-4">
            <div
              className={`flex flex-col items-center p-4 rounded-lg transition-all ${step >= 1 ? "bg-blue-500/20 border border-blue-500/50" : "bg-slate-800/50 border border-slate-700"}`}
            >
              <div
                className={`w-10 h-10 rounded-full flex items-center justify-center font-bold mb-2 ${step >= 1 ? "bg-blue-500 text-white" : "bg-slate-700 text-slate-400"}`}
              >
                {step > 1 ? <CheckCircle className="w-5 h-5" /> : "1"}
              </div>
              <p className="text-xs text-center text-slate-300">Complete seu perfil</p>
            </div>
            <div
              className={`flex flex-col items-center p-4 rounded-lg transition-all ${step >= 2 ? "bg-blue-500/20 border border-blue-500/50" : "bg-slate-800/50 border border-slate-700"}`}
            >
              <div
                className={`w-10 h-10 rounded-full flex items-center justify-center font-bold mb-2 ${step >= 2 ? "bg-blue-500 text-white" : "bg-slate-700 text-slate-400"}`}
              >
                {step > 2 ? <CheckCircle className="w-5 h-5" /> : "2"}
              </div>
              <p className="text-xs text-center text-slate-300">Configure preferências</p>
            </div>
            <div
              className={`flex flex-col items-center p-4 rounded-lg transition-all ${step >= 3 ? "bg-blue-500/20 border border-blue-500/50" : "bg-slate-800/50 border border-slate-700"}`}
            >
              <div
                className={`w-10 h-10 rounded-full flex items-center justify-center font-bold mb-2 ${step >= 3 ? "bg-blue-500 text-white" : "bg-slate-700 text-slate-400"}`}
              >
                {step > 3 ? <CheckCircle className="w-5 h-5" /> : "3"}
              </div>
              <p className="text-xs text-center text-slate-300">Adicione WhatsApp</p>
            </div>
          </div>

          {/* Sign Up Card */}
          <Card className="border-slate-700 bg-slate-900">
            <CardHeader>
              <CardTitle className="text-2xl text-white">Criar Conta</CardTitle>
              <CardDescription className="text-slate-400">Preencha seus dados para começar</CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSignUp} className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="fullName" className="text-slate-200">
                    Nome Completo
                  </Label>
                  <Input
                    id="fullName"
                    type="text"
                    placeholder="João Silva"
                    required
                    value={fullName}
                    onChange={(e) => setFullName(e.target.value)}
                    className="border-slate-600 bg-slate-800 text-white placeholder-slate-500 focus-visible:ring-blue-500"
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="email" className="text-slate-200">
                    Email
                  </Label>
                  <Input
                    id="email"
                    type="email"
                    placeholder="seu@email.com"
                    required
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    className="border-slate-600 bg-slate-800 text-white placeholder-slate-500 focus-visible:ring-blue-500"
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="password" className="text-slate-200">
                    Senha
                  </Label>
                  <Input
                    id="password"
                    type="password"
                    required
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    className="border-slate-600 bg-slate-800 text-white placeholder-slate-500 focus-visible:ring-blue-500"
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="repeatPassword" className="text-slate-200">
                    Confirmar Senha
                  </Label>
                  <Input
                    id="repeatPassword"
                    type="password"
                    required
                    value={repeatPassword}
                    onChange={(e) => setRepeatPassword(e.target.value)}
                    className="border-slate-600 bg-slate-800 text-white placeholder-slate-500 focus-visible:ring-blue-500"
                  />
                </div>

                {error && (
                  <div className="flex gap-2 rounded-lg bg-red-500/10 p-3 text-sm text-red-400">
                    <AlertCircle className="h-4 w-4 shrink-0 mt-0.5" />
                    {error}
                  </div>
                )}

                <Button
                  type="submit"
                  className="w-full bg-gradient-to-r from-blue-500 to-emerald-500 text-white hover:from-blue-600 hover:to-emerald-600"
                  disabled={isLoading}
                >
                  {isLoading ? "Criando conta..." : "Criar Conta"}
                </Button>

                <div className="text-center text-sm text-slate-400">
                  Já tem uma conta?{" "}
                  <Link href="/auth/login" className="text-blue-400 underline underline-offset-4 hover:text-blue-300">
                    Faça login
                  </Link>
                </div>
              </form>
            </CardContent>
          </Card>

          {/* Onboarding Info */}
          <div className="bg-slate-800/50 border border-slate-700 rounded-lg p-6 space-y-4">
            <h3 className="text-lg font-semibold text-white">O que você fará após criar a conta:</h3>
            <ul className="space-y-3">
              <li className="flex gap-3">
                <div className="w-6 h-6 rounded-full bg-blue-500/20 border border-blue-500/50 flex items-center justify-center flex-shrink-0 mt-0.5">
                  <span className="text-blue-400 text-sm font-bold">1</span>
                </div>
                <div>
                  <p className="text-white font-medium">Complete seu perfil</p>
                  <p className="text-slate-400 text-sm">Atualize seu nome, cidade e número de WhatsApp</p>
                </div>
              </li>
              <li className="flex gap-3">
                <div className="w-6 h-6 rounded-full bg-emerald-500/20 border border-emerald-500/50 flex items-center justify-center flex-shrink-0 mt-0.5">
                  <span className="text-emerald-400 text-sm font-bold">2</span>
                </div>
                <div>
                  <p className="text-white font-medium">Configure suas preferências</p>
                  <p className="text-slate-400 text-sm">Selecione a área de trabalho e países de interesse</p>
                </div>
              </li>
              <li className="flex gap-3">
                <div className="w-6 h-6 rounded-full bg-purple-500/20 border border-purple-500/50 flex items-center justify-center flex-shrink-0 mt-0.5">
                  <span className="text-purple-400 text-sm font-bold">3</span>
                </div>
                <div>
                  <p className="text-white font-medium">Receba ofertas no WhatsApp</p>
                  <p className="text-slate-400 text-sm">Vagas personalizadas serão enviadas direto para você</p>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  )
}
